<template>
  <div class="dynamic-table">
    <div class="add-btn">
      <q-button
        :disabled="addDisabled"
        type="primary"
        icon="qax-icon-Alone-Plus"
        @click="handleAdd"
      >
        增加一组
      </q-button>
    </div>
    <div class="content">
      <div
        class="group-list"
        v-for="(groupItem, index) in formData.tableData"
        :key="index"
      >
        <div class="group-tool">
          <div class="group-name">
            组{{ index + 1 }}
            <q-select
              v-model="groupItem.centerId"
              placeholder="请选择运营中心"
              clearable
              @change="changeGroup($event, index)"
            >
              <q-option
                v-for="item in centerList"
                :key="item.value"
                :label="item.label"
                :value="item.value"
                :disabled="item.disabled"
              >
              </q-option>
            </q-select>
          </div>
          <q-button
            icon="qax-icon-Trash"
            type="text"
            title="删除"
            class="delete"
            danger
            @click="handleDelete(index)"
          />
        </div>
        <q-table
          ref="tableRef"
          :data="groupItem.list"
          class="edit-table w-full mt-8"
        >
          <q-table-column
            v-for="item in tableHeadData"
            :key="item.prop"
            v-bind="item"
          >
            <template #default="{ row }">
              <component
                :is="item.component"
                v-model="row[item.prop]"
                v-bind="item.componentProps"
              />
            </template>
          </q-table-column>
        </q-table>
      </div>
    </div>
  </div>
</template>
<script>
import CustomSelect from './custom-select';
import CustomString from './custom-string';
import CustomInput from './custom-input';
import { cloneDeep } from 'lodash';

export default {
  name: 'DynamicTable',
  components: {
    CustomSelect,
    CustomString,
    CustomInput,
  },
  props: {
    dataIds: {
      type: Array,
      default: () => [],
    },
    tableHeadData: {
      type: Array,
      default: () => [],
    },
    disabled: {
      type: Boolean,
      default: true,
    },
    // 是否可以自定义增减行数
    isCustomRow: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      formData: {
        tableData: [],
      },
      form: {
        virtualList: [],
      },
      centerList: [
        {
          label: '运营中心1',
          value: '1',
        },
        {
          label: '运营中心2',
          value: '2',
        },
        {
          label: '运营中心3',
          value: '3',
        },
      ],
    };
  },
  computed: {
    newRow() {
      const row = {};
      this.tableHeadData.forEach((item) => {
        // 多选要设置成[] 解决初次校验报错的问题
        row[item.prop] = item.componentProps?.multiple ? [] : '';
      });
      return row;
    },
    addDisabled() {
      return !this.isCustomRow || this.disabled;
    },
  },

  methods: {
    setPeriodList(length, period) {
      return Array.from({ length }, () => ({
        ...this.newRow,
        period,
      }));
    },
    handleAdd() {
      const list48 = this.setPeriodList(6, '48');
      const list7 = this.setPeriodList(7, '7');
      const list30 = this.setPeriodList(30, '30');
      const list90 = this.setPeriodList(90, '90');

      const row = {
        centerId: '',
        list: [...list48, ...list7, ...list30, ...list90],
        _index: this.formData.tableData.length,
      };
      this.formData.tableData.push(row);
    },
    handleDelete(index) {
      const findIndex = this.formData.tableData.findIndex(
        (item) => item._index === index
      );
      this.formData.tableData.splice(findIndex, 1);
    },
    getValues() {
      const tableData = cloneDeep(this.formData.tableData);
      // 用索引设置dataId
      tableData.forEach((item, index) => {
        this.dataIds.forEach((key) => {
          item[key] = index + 1;
        });
      });
      return tableData;
    },
    setValues(data) {
      this.formData.tableData = data;
      this.changeGroup();
    },
    async validate() {
      return await this.$refs.formRef.validate();
    },
    changeGroup() {
      this.centerList = this.centerList.map((item) => {
        return {
          ...item,
          disabled: this.formData.tableData?.some(
            (e) => e.centerId === item.value
          ),
        };
      });
    },
  },
};
</script>

