<template>
  <q-switch :value="value" @change="onChange" />
</template>

<script>
import { defineComponent } from '@vue/composition-api';

export default defineComponent({
  name: 'BooleanSetter',
  props: {
    value: {
      type: Boolean,
      default: false
    },
    onChange: {
      type: Function,
      default: () => {}
    }
  },
});
</script>
