<template>
  <div class="checkbox-setter">
    <q-checkbox-group v-model="_value">
      <q-checkbox
        v-for="(item, index) in options"
        :key="index"
        :label="item.value"
        >{{ item.label }}
      </q-checkbox>
    </q-checkbox-group>
  </div>
</template>

<script >
import { computed, defineComponent } from '@vue/composition-api';

export default defineComponent({
  name: 'CheckboxSetter',
  props: {
    value: {
      type: Array,
      default: () => [],
    },
    options: {
      type: Array,
      default: () => [],
    },
    onChange: {
      type: Function,
      default: () => {},
    },
  },
  setup(props) {
    return {
      _value: computed({
        get: () => props.value,
        set: (value) => props.onChange(value),
      }),
    };
  },
});
</script>
<style lang="scss" scoped>
.checkbox-setter {
  clear: both;
  margin-left: -68px;
}
</style>
