<template>
  <q-select v-bind="$attrs" v-on="$listeners">
    <template #prefix>
      <slot name="prefix" />
    </template>
    <template #empty>
      <slot name="empty" />
    </template>
    <q-option
      v-for="item in options"
      :key="item[config.value]"
      :label="item[config.label]"
      :value="item[config.value]"
      :disabled="item[config.disabled]"
    >
      <slot name="option" v-bind="item" />
    </q-option>
  </q-select>
</template>



<script >
const __sfc_main = {
  name: 'CustomSelect',
  inheritAttrs: false
};
__sfc_main.props = {
  options: {
    type: Array,
    default: () => []
  },
  config: {
    type: Object,
    default: () => ({
      value: 'value',
      label: 'label',
      disabled: 'disabled'
    })
  }
};
export default __sfc_main;
</script>
