import { cloneDeep } from 'lodash';
export class Workbench {
    engine;
    assets;
    setters;
    settersFormInstance = null;
    constructor(engine) {
        this.engine = engine;
        this.assets = {
            components: {},
            menu: []
        };
        this.setters = {};
    }
    updateAssets(assets) {
        this.assets = cloneDeep(assets);
    }
    updateSetters(setters) {
        this.setters = cloneDeep(setters);
    }
    validateSettersForm() {
        if (this.settersFormInstance) {
            return this.settersFormInstance.validate();
        }
        return Promise.resolve(true);
    }
}
