import { uid } from '@designable/shared';
import { get, cloneDeep, isEmpty } from 'lodash';
export class Screen {
    id;
    props;
    componentName;
    componentMeta;
    type;
    constructor(screen) {
        this.id = screen.id || `screen_${uid()}`;
        this.props = screen.props || this.meta2Props(screen.componentMeta);
        this.componentName = screen.componentName;
        this.componentMeta = screen.componentMeta;
        this.type = screen.type;
    }
    get propsMeta() {
        return get(this.componentMeta, 'props', []);
    }
    meta2Props(componentMeta) {
        const result = {};
        const { props } = componentMeta;
        props.forEach(item => {
            result[item.name] = cloneDeep(item.defaultValue);
        });
        return result;
    }
    validator() {
        for (const item of this.propsMeta) {
            if (get(item, 'rules.type') === 'string' && item.rules.required) {
                if (isEmpty(this.props?.[item.name])) {
                    new Error(`${item.desc}不能为空`);
                    return;
                }
            }
            if (get(item, 'rules.type') === 'array' && item.rules.required) {
                const value = this.props?.[item.name];
                if (isEmpty(value)) {
                    new Error(`${item.desc}不能为空`);
                    return;
                }
            }
        }
        return true;
    }
    generateValidateRules() {
        const rules = {};
        for (const item of this.propsMeta) {
            if (get(item, 'rules.type') === 'string' && item.rules.required) {
                const validator = (rule, value, callback) => {
                    if (isEmpty(this.props?.[item.name])) {
                        callback(new Error(`${item.desc}不能为空`));
                        return;
                    }
                    callback();
                };
                rules[item.name] = [
                    { required: true, validator, trigger: 'blur' }
                ];
            }
            if (get(item, 'rules.type') === 'array' && item.rules.required) {
                const validator = (rule, value, callback) => {
                    if (isEmpty(value)) {
                        callback(new Error(`${item.desc}不能为空`));
                        return;
                    }
                    callback();
                };
                rules[item.name] = [
                    { required: true, validator, message: `${item.desc}不能为空`, trigger: 'blur' }
                ];
            }
        }
        return rules;
    }
    getSchema() {
        return {
            id: this.id,
            type: this.type,
            componentName: this.componentName,
            props: this.props || {},
        };
    }
}
