<template>
  <div class="edit-mock-setter">
    <div class="edit-btn">
      <q-button @click="handleDrawer" type="text"> 编辑静态数据 </q-button>
      <q-tooltip>
        <div slot="content">
          动态数据：{{ dataDrilling ? '支持' : '不支持' }}下钻 <br />
          静态数据：{{ mockDrilling ? '支持' : '不支持' }}下钻
        </div>
        <i class="qax-icon-Question-outline tooltip-icon"></i>
      </q-tooltip>
    </div>
    <q-drawer
      title="编辑静态数据"
      size="800px"
      :wrapperClosable="false"
      :visible.sync="drawer"
    >
      <!-- mock -->
      <q-tabs v-model="activeMode" type="card" :before-leave="onBeforeTabLeave">
        <q-tab-pane label="可视模式" name="table">
          <DynamicTable
            v-if="activeTableTab"
            ref="dynamicTableRef"
            :table-head-data="tableHeadData"
            :data-ids="tableDataIds"
            :is-custom-row="isCustomRow"
            :disabled="false"
          />
        </q-tab-pane>
        <q-tab-pane label="Json模式" name="json">
          <JsonEditor ref="jsonEditorRef" :data-ids="tableDataIds" />
        </q-tab-pane>
      </q-tabs>
      <span slot="footer" class="dialog-footer">
        <q-button type="primary" @click="handleSave">确定</q-button>
      </span>
    </q-drawer>
  </div>
</template>


<script >
import { ref, getCurrentInstance } from '@vue/composition-api';
import DynamicTable from './components/dynamic-table';
import JsonEditor from './components/json-editor';
const __sfc_main = {
  name: 'EventSetter'
};
__sfc_main.props = {
  value: {
    type: String,
    default: ''
  },
  dataDrilling: {
    type: Boolean,
    default: true
  },
  mockDrilling: {
    type: Boolean,
    default: true
  },
  onChange: {
    type: Function,
    default: () => {}
  }
};
__sfc_main.setup = (__props, __ctx) => {
  const props = __props;
  const _value = ref(JSON.parse(props.value));
  const {
    proxy
  } = getCurrentInstance();
  const drawer = ref(false);
  const activeMode = ref('table');
  const tableHeadData = ref([{
    label: '时间周期',
    prop: 'period',
    component: 'CustomString'
  }, {
    label: '攻击者IP',
    prop: 'ip',
    required: true,
    component: 'CustomInput'
  }, {
    label: '攻击者国家',
    prop: 'country',
    required: true,
    component: 'CustomInput'
  }, {
    label: '攻击次数',
    prop: 'attackNum',
    required: true,
    component: 'CustomInput',
    componentProps: {
      type: 'number'
    }
  }]);
  const tableDataIds = ref([]);
  const isCustomRow = ref(true);
  const activeTableTab = ref(true);
  const dynamicTableRef = ref(null);
  const jsonEditorRef = ref(null);
  const onBeforeTabLeave = async (to, from) => {
    // 切换至可视模式
    if (to === 'table' && from === 'json') {
      const isOk = jsonEditorRef.value.validate();
      if (!isOk) {
        proxy.$message.error('JSON格式不正确，无法转换为可视数据!');
        return false;
      }
      const data = jsonEditorRef.value.getValues();
      activeTableTab.value = false;
      await nextTick();
      activeTableTab.value = true;
      await nextTick();
      dynamicTableRef.value.setValues(data);
    } else if (to === 'json' && from === 'table') {
      // 切换至JSON模式
      const data = dynamicTableRef.value.getValues();
      jsonEditorRef.value.setValues(data);
    }
    return true;
  };
  const handleDrawer = async () => {
    drawer.value = true;
    await nextTick();
    dynamicTableRef.value.setValues(_value.value);
  };
  const handleSave = () => {
    const data = dynamicTableRef.value.getValues();
    const json = JSON.stringify(data, null, 2);
    props.onChange(json);
    drawer.value = false;
  };
  return {
    drawer,
    activeMode,
    tableHeadData,
    tableDataIds,
    isCustomRow,
    activeTableTab,
    dynamicTableRef,
    jsonEditorRef,
    onBeforeTabLeave,
    handleDrawer,
    handleSave
  };
};
__sfc_main.components = Object.assign({
  DynamicTable,
  JsonEditor
}, __sfc_main.components);
export default __sfc_main;
</script>
<style lang="scss" scoped>
.edit-mock-setter {
  margin-top: 9px;
  .edit-btn {
    display: flex;
    .tooltip-icon {
      margin-left: 4px;
      font-size: 12px;
      color: #999;
      cursor: pointer;
      margin-top: 2px;
    }
  }
}
::v-deep .q-tabs {
  height: 100%;
  display: flex;
  flex-direction: column;
  .q-tabs__header {
    flex: 0;
  }
  .q-tabs__content {
    flex: 1;
    .q-tab-pane {
      height: 100%;
    }
  }
}
</style>


