<template>
  <q-input
    v-model="json"
    type="textarea"
    :rows="12"
    placeholder="仅支持JSON格式"
    :disabled="disabled"
  />
</template>

<script >
import { ref } from '@vue/composition-api';
const __sfc_main = {};
__sfc_main.props = {
  dataIds: {
    type: Array,
    default: () => []
  },
  disabled: {
    type: Boolean,
    default: false
  }
};
__sfc_main.setup = (__props, __ctx) => {
  const props = __props;
  const json = ref('');
  const validate = () => {
    let data;
    try {
      data = JSON.parse(json.value);
    } catch (err) {
      console.log(err);
      return false;
    }
    if (typeof data !== 'object') {
      return false;
    }
    return true;
  };
  const getValues = () => {
    const data = JSON.parse(json.value);
    if (Array.isArray(data)) {
      data.forEach((item, index) => {
        props.dataIds.forEach(key => {
          item[key] = index + 1;
        });
      });
    }
    return data;
  };
  const setValues = data => {
    json.value = JSON.stringify(data, null, 2);
  };
  return Object.assign({
    json
  }, {
    setValues,
    getValues,
    validate
  });
};
export default __sfc_main;
</script>
